'use client';

// 導入時就自動轉為JS資料格式
import products from './_data/products.json';
// next 提供的圖片自動最佳化
import Image from 'next/image';
import './_styles/product-table.css';

export default function ProductTablePage() {
  // console.log(products)
  return (
    <>
      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th>圖片</th>
            <th>名稱</th>
            <th>價格</th>
          </tr>
        </thead>
        <tbody>
          {products.map((product) => {
            return (
              <tr key={product.id}>
                <td>{product.id}</td>
                <td>
                  <Image
                    src={product.image_url}
                    width={100}
                    height={100}
                    alt={product.name}
                  />
                </td>
                <td>{product.name}</td>
                <td>{product.price}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </>
  );
}
